.data-section {
    &-header {
        position: relative;
    }

    &-title {
        border-bottom: 1px solid $dark;
        padding: 10px 100px 10px 0;
        font-size: 24px;
        font-weight: normal;
    }

    &-btn {
        position: absolute;
        top: 10px;
        right: 0;
        width: 100px;
        height: $input-height;
        font-size: $input-font-size;
        color: $white;
        background: $primary;
        border: none;

        &:hover {
            background: $primary-hover;
            cursor: pointer;
        }
    }
}